<template>
    <Layout class="layout">
        <Content class="login-box">
        	<div class="-txt"></div>
	        <div class="-desc">
	        	<span><img src="~@static/images/corporation.png"></span>
	        	<span><img src="~@static/images/customer_manage.png"></span>
	        	<span><img src="~@static/images/service_manage.png"></span>
	        	<span><img src="~@static/images/terminal_manage.png"></span>
	        </div>
        	<Form ref="formValidate" :model="formValidate" :rules="ruleValidate" class="login-form" @keydown.enter.native="login('formValidate')">
        		<div class="login-form_title">登录</div>
        		<FormItem prop="user_name">
		            <Input type="text" v-model="formValidate.username" size="large" placeholder="用户名">
		                <Icon type="ios-person" size="20" slot="prepend"></Icon>
		            </Input>
		        </FormItem>
		        <FormItem prop="password">
		            <Input type="password" v-model="formValidate.password" size="large" placeholder="密码">
		                <Icon type="ios-lock" size="20" slot="prepend"></Icon>
		            </Input>
		        </FormItem>
		        <FormItem prop="code">
		        	<Row>
		                <Col span="15">
		                    <Input type="text" v-model="formValidate.kaptcha" size="large" placeholder="验证码">
				                <Icon type="ios-unlock" size="20" slot="prepend"></Icon>
				            </Input>
		                </Col>
		                <Col span="8" offset="1">
		                    <img :src="codeImg" @click="checkImg" class="code-img">
		                </Col>
		            </Row>
		        </FormItem>
		        <div class="-m-bottom1">
		            <Button type="primary" @click="login('formValidate')" size="large" long>登录</Button>
		        </div>
		    </Form>
        </Content>
        <div class="login-footer">
	        浙江众马科技有限公司 版权所有 浙ICP备16027614号-1 Copyright@2017 www.zhongmakj.com
	    </div>
    </Layout>
</template>

<script>
import { mapActions } from 'vuex';
import axios from 'axios';
import sha256 from 'js-sha256';

import { baseUrl } from '@lib/config';

export default {
	data() {
		return {
			formValidate: {
				username: '',
				password: '',
				kaptcha: '',
			},
			codeImg: '',
			ruleValidate: {
				username: [
					{ required: true, message: '用户名错误', trigger: 'blur' }
				],
				password: [
					{ required: true, message: '密码错误', trigger: 'blur' },
					{ type: 'string', min: 3, message: '密码不能少于6位', trigger: 'blur' }
				],
				kaptcha: [
					{ required: true, message: '验证码错误', trigger: 'blur' },
					{ type: 'string', min: 4, message: '验证码错误', trigger: 'blur' }
				]
			}
		}
	},
	created() {
		this.checkImg();
	},
	methods: {
		...mapActions(['USER_SIGNIN', 'USER_MENUS','setShopsList', 'setMerchantList']),
		login(form) {
			this.$refs[form].validate((valid) => {
				if (valid) {
					axios.post(baseUrl + "user/login", {
						username: this.formValidate.username,
						password: sha256(this.formValidate.password).toUpperCase(),
						kaptcha: this.formValidate.kaptcha
					}).then( response => {
						if (response.data.respCode == '000020') {
							const User = {
                                ...response.data.respData,
								userName: this.formValidate.username
                            }
                            
							this.USER_SIGNIN(User).then(() => {
								axios.post(baseUrl + 'menu/list',{}).then(res => { 
									if (res.data.respCode == '000000') {
										this.USER_MENUS(res.data.respData).then(() => {
											this.$router.replace({ path: this.$route.query.redirect ? this.$route.query.redirect : '/home' });
										})
									} else {
										this.$Message.error(res.data.respDesc)
									}
								}).catch(error => {
									this.$Message.error(error)
								});
                            });
                            // 获取当前用户下的所有门店信息
                            this.setShopsList()
                            // 获取当前用户下的所有商户
                            this.setMerchantList()
						} else {
							this.checkImg();
							this.$Message.error(response.data.respDesc);
						}
					}).catch(error => {
						this.$Message.error(error);
					});
				}
			})
		},
		checkImg() {
			this.codeImg = baseUrl + 'kaptchaImage?a=' + Math.random();
		}
	}
}
</script>

<style lang="less" rel="stylesheet/less">
.layout {
  height: 100vh;
}
.login-box {
  height: 100vh;
  background: url("~@static/images/banner.jpg") center no-repeat;
  background-size: cover;
  position: relative;
  .-txt {
    color: rgba(255, 255, 255, 0.82);
    position: absolute;
    top: 30.3%;
    right: 56%;

    h2 {
      font-weight: 100;
      font-size: 60px;
    }
    h3 {
      font-weight: 100;
      font-size: 38px;
      color: rgba(255, 255, 255, 0.62);
    }
    h4 {
      font-weight: 100;
      font-size: 32px;
      color: rgba(255, 255, 255, 0.42);
    }
  }
  .-desc {
    position: absolute;
    bottom: 10%;
    left: 20%;
    right: 20%;

    span {
      display: inline-block;
      width: 24%;
      text-align: center;
      @media screen and (max-width: 720px) {
        img {
          width: 80%;
        }
      }
    }
  }
  @media screen and (max-width: 720px) {
    .-txt {
      top: 3%;
      right: 0;
      left: 0;
      text-align: center;
      h2 {
        font-size: 30px;
      }
      h3 {
        font-size: 20px;
      }
      h4 {
        font-size: 18px;
      }
    }
    .-desc {
      left: 5%;
      right: 5%;
      bottom: 12%;
    }
  }
  .login-form {
    width: 320px;
    position: absolute;
    top: 28%;
    left: 60%;
    padding: 10px 20px;
    background: rgba(255, 255, 255, 0.82);
    border-radius: 10px;

    .code-img {
      height: 36px;
      width: 100%;
    }

    &_title {
      font-weight: 100;
      color: #fff;
      font-size: 20px;
      margin-bottom: 10px;
    }
    @media screen and (max-width: 720px) {
      &_title {
        font-size: 16px;
      }
    }
  }
  @media screen and (max-width: 720px) {
    .login-form {
      width: auto;
      left: 5%;
      right: 5%;
      top: 24%;
    }
  }
}

.login-footer {
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  color: rgba(255, 255, 255, 0.42);
}
</style>


